
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import {httpUrl} from '../../http_url';

export default {
name: 'HelloWorld',
data () {
return {
show: true,
time:60,
start: false,
phone: '',
identifyCode:'',
password:'',
passwordAgain:''
}
},
components: {
Group,
Cell,
XInput,
XButton, Alert,Countdown
},
methods: {
getCode(){
console.log(this.SpaceTransformation(this.phone));
let postData = this.$qs.stringify({
phone:this.SpaceTransformation(this.phone),
type:101
})
this.$axios({
method: 'post',
url:httpUrl.getCode,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$vux.toast.show({
text: '验证码发送成功'
});
this.show = false;
this.start = true;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
finish (index) {
this.start = false;
this.time = 60;
this.show = true;
},
blurPassword(){
const _this = this;
if(this.isEmpty(this.password)){
this.$vux.alert.show({
title: '错误提示',
content: '请输入密码',
onHide () {

}
})
return
}else if(!this.Val.isPassWord(this.password) || this.password.length<6){
this.$vux.alert.show({
title: '错误提示',
content: '新密码必须为6位以上的英文和数字组成',
onHide () {
_this.password = ''
}
})
}
},
blurPasswordAgain(){
const _this = this;
if(this.isEmpty(this.passwordAgain)){
this.$vux.alert.show({
title: '错误提示',
content: '请输入密码',
onHide () {

}
})
return
}else if(this.passwordAgain != this.password){
this.$vux.alert.show({
title: '错误提示',
content: '两次密码输入不一致',
onHide () {
_this.passwordAgain = ''
}
})
}
},
submit(){
const _this = this;
let postData = this.$qs.stringify({
type:10,
phone:this.SpaceTransformation(this.phone),
code:this.identifyCode,
password:this.password,
ckpassword:this.passwordAgain,
})

if(this.isEmpty(this.passwordAgain)){
this.$vux.alert.show({
title: '错误提示',
content: '请输入密码',
onHide () {

}
})
return
}else if(this.passwordAgain != this.password){
this.$vux.alert.show({
title: '错误提示',
content: '两次密码输入不一致',
onHide () {
_this.passwordAgain = ''
}
})
return
}else {
this.$axios({
method: 'post',
url:httpUrl.setPassword,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$vux.toast.show({
text: '密码设置成功',
type:'success',
time:2000
});
this.$router.push('/login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
}

}
}
}



